@import "variables";
@import "common";
@import "forms";
@import "header";

/* home */
body.main-page{
	background: $color-1;
	#wrapper{
		background: url(../images/bg-main.jpg) center center no-repeat;
		background-size: cover;
	}
	.main-block{
		text-align: center;
		padding: 65px 15px 0;
		color: #FFFFFF;
		font-weight: 300;
		font-size: 14px;
		margin: auto;
		.title{
			color: #fff;
			font-weight: bold;
			font-size: 62px;
			line-height: 1.1em;
			text-transform: uppercase;
			margin: 0;
		}
		.subtitle{
			font-weight: 100;
			font-size: 32px;
			margin-bottom: 24px;
			text-transform: none;
			display: flex;
			align-items: center;
			&:before, &:after{
				content: '';
				border-bottom: 1px solid #fff;
				flex-grow: 1;
				margin: 8px 5px 0;
			}
			&:before{
				margin-right: 33px;
			}
			&:after{
				margin-left: 33px;
			}
		}
		p{
			margin-bottom: 34px;
			text-transform: uppercase;
		}
		br{
			display: none;
		}
	}
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	body.main-page{
		#wrapper{
			flex-direction: row;
			align-items: center;
			justify-content: center;
			height: 100%;
			.main-block{
				margin: 0;
			}
		}
	}
}
@media (max-width: 767px) {
	body.main-page{
		#wrapper{
			background-image: url(../images/bg-main-mob.jpg) ;
		}
		.main-block{
			padding: 15px 15px;
			font-size: 10px;
			.title{
				font-size: 26px;
				margin-bottom: 9px;
			}
			.subtitle{
				font-size: 18px;
				margin-bottom: 31px;
				&:before, &:after{
					margin: 0px 3px 0;
				}
				&:before{
					margin-right: 7px;
				}
				&:after{
					margin-left: 7px;
				}
			}
			p{
				margin-bottom: 37px;
			}
			br{
				display: inherit;
			}
		}
	}
}

/* sidebar */
.sidebar{
	padding-top: 10px;
	margin-bottom: 40px;
}
.department-info{
	background: rgba(255, 255, 255, 0.7);
	border: 1px solid #FFFFFF;
	border-radius: 10px;
	margin-bottom: 12px;
	text-align: center;
	padding: 40px 10px;
	font-weight: 300;
	font-size: 14px;
	line-height: 157%;
	color: #2F2F2F;
	.image{
		margin-bottom: 16px;
	}
	.department-name{
		margin-bottom: 20px;
		font-size: 18px;
		font-weight: 700;
		line-height: 1.45em;
		color: $color-1;
	}
	.description{
		padding: 0 5px;
		p:last-child{
			margin-bottom: 0;
		}
	}
}

.sidebar-nav{
	margin-bottom: 8px;
	ul{
		list-style: none;
		margin: 0;
		padding: 0;
		li{
			margin-bottom: 4px;
			a{
				display: flex;
				justify-content: center;
				align-items: center;
				text-align: center;
				background: rgba(255, 255, 255, 0.7);
				border: 1px solid #FFFFFF;
				border-radius: 10px;
				padding: 10px;
				min-height: 52px;
				font-weight: 300;
				font-size: 14px;
				color: #2F2F2F;
				text-decoration: none;
			}
		}
	}
}

/* row-services */
.row-services{
	margin-bottom: 7px;
	.item-wrap{
		margin-bottom: 57px;
		display: flex;
		flex: 0 0 33.33%;
		max-width: 33.33%;
	}
	.item{
		width: 100%;
		background: #FFFFFF;
		border: 1px solid #FFFFFF;
		box-shadow: 0px 4px 50px rgba(31, 78, 120, 0.08);
		border-radius: 8px;
		position: relative;
		padding: 20px 15px;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		min-height: 126px;
		&:after{
			content: '';
			position: absolute;
			left: 50%;
			top: 100%;
			transform: translate(-50%,0);
			width: 80px;
			height: 28px;
			background: url(../images/service-bottom.svg) center center no-repeat;
			background-size: contain;
			z-index: 0;
			
		}
		.title{
			font-weight: 300;
			font-size: 14px;
			line-height: 157%;
			color: #2F2F2F;
			margin: 0;
		}
		.readmore{
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translate(-50%,-50%);
			background: #62C9AA;
			border: 1px solid rgba(255, 255, 255, 0.89);
			box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54);
			width: 36px;
			height: 36px;
			border-radius: 50%;
			z-index: 1;
			&:after{
				content: '';
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
				width: 6px;
				height: 11px;
				background: url(../images/arrow.svg) center center no-repeat;
				background-size: contain;
			}
		}
	}
}

@media (max-width: 991px) {
	.row-services{
		.item-wrap{
			margin-bottom: 45px;
			flex: 0 0 50%;
			max-width: 50%;
		}
	}
}
@media (max-width: 575px) {
	.row-services{
		margin-bottom: 14px;
		.item-wrap{
			margin-bottom: 10px;
			flex: 0 0 100%;
			max-width: 100%;
			padding-right: 25px;
		}
		.item{
			padding: 15px 20px 15px 10px;
			min-height: 90px;
			&:after{
				left: 100%;
				top: 50%;
				transform: translate(-18px,-50%) rotate(-90deg);
				width: 54px;
				height: 20px;
				background: url(../images/service-bottom.svg) center center no-repeat;
				background-size: contain;
				z-index: 0;
				
			}
			.readmore{
				top: 50%;
				left: 100%;
				width: 28px;
				height: 28px;
				&:after{
					width: 5px;
					height: 8px;
				}
			}
		}
	}
}

/* main-departmets */
.departmets-page{
	#wrapper{
		background: linear-gradient(180deg, #1F4E78 0%, #3B7BB5 100%);
	}
	.main-header{
		background: none;
	}
	h1{
		text-align: center;
		color: #fff;
		margin-bottom: 104px;
	}
}
@media (max-width: 991px) {
	.departmets-page{
		.main-header{
			box-shadow: 0px -1px 11px rgba(0, 0, 0, 0.25);
			&:after{
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				border-bottom: 1px solid rgba(59, 123, 181, 0.41);
			}
		}
	}
}

.row-departmets{
	.item-wrap{
		margin-bottom: 98px;
		display: flex;
		flex: 0 0 25%;
		max-width: 25%;
	}
	.item{
		width: 100%;
		background: #1F4E78;
		box-shadow: -7px -7px 33px #5189BC, 7px 7px 47px rgba(0, 0, 0, 0.6), inset -1px -1px 10px rgba(31, 78, 120, 0.23);
		border-radius: 20px;
		padding: 40px 15px 47px;
		color: #fff;
		text-align: center;
		position: relative;
		border: 2px solid $color-1a;
		&:after{
			content: '';
			content: '';
			position: absolute;
			left: 50%;
			top: 100%;
			transform: translate(-50%,0);
			width: 100px;
			height: 32px;
			background: url(../images/department-bottom.svg) center center no-repeat;
			background-size: contain;
			z-index: 0;
			
		}
		.image{
			margin-bottom: 23px;
			min-height: 163px;
			display: flex;
			align-items: center;
			justify-content: center;
			img{
				max-height: 100%;
			}
		}
		.title{
			font-style: normal;
			font-weight: 100;
			font-size: 18px;
			line-height: 142%;
			color: #FFFFFF;
			strong{
				font-weight: 400;
			}
		}
		.readmore{
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translate(-50%,-50%);
			background: #62C9AA;
			border: 1px solid rgba(255, 255, 255, 0.89);
			box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54);
			width: 40px;
			height: 40px;
			border-radius: 50%;
			z-index: 1;
			&:after{
				content: '';
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
				width: 7px;
				height: 12px;
				background: url(../images/arrow.svg) center center no-repeat;
				background-size: contain;
			}
		}
	}
}

@media (max-width: 1199px) {
	.row-departmets{
		.item-wrap{
			flex: 0 0 33.33%;
			max-width: 33.33%;
		}
	}
}
@media (max-width: 991px) {
	.departmets-page{
		h1{
			margin-bottom: 80px;
		}
	}
	.row-departmets{
		.item-wrap{
			flex: 0 0 50%;
			max-width: 50%;
		}
	}
}
@media (max-width: 767px) {
	.departmets-page{
		h1{
			margin-bottom: 50px;
		}
	}
	.row-departmets{
		.item-wrap{
			margin-bottom: 60px;
		}
	}
}
@media (max-width: 575px) {
	.departmets-page{
		.main{
			padding-top: 20px;
		}
		h1{
			margin-bottom: 20px;
			padding: 0;
			&:after{
				display: none;
			}
		}
	}
	.row-departmets{
		.item-wrap{
			margin-bottom: 60px;
			flex: 0 0 100%;
			max-width: 100%;
			padding: 0 23px;
		}
		.item{
			box-shadow: none;
		}
	}
}

/* payment-services */
.payment-services-left, .payment-card-left{
	flex: 0 0 43.5%;
	max-width: 43.5%;
	margin-bottom: 25px;
	font-size: 12px;
	line-height: 1.8em;
}

.form-payment{
	.summ{
		color: $color-1;
		margin-bottom: 7px;
		font-weight: 700;
		font-size: 14px;
		text-transform: uppercase;
		span{
			font-size: 10px;
		}
	}
	p{
		font-size: 12px;
		line-height: 22px;
		margin-bottom: 28px;
	}
}

.payment-services-right{
	flex: 0 0 56.5%;
	max-width: 56.5%;
	margin-bottom: 25px;
	padding-left: 32px;
}

@media (max-width: 991px) {
	.payment-services-left, .payment-card-left{
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 35px;
	}
	.payment-services-right{
		flex: 0 0 100%;
		max-width: 100%;
		margin-bottom: 25px;
		padding-left: 12px;
	}
	.payment-card-right{
		flex: 0 0 100%;
		max-width: 100%;
	}
}
@media (max-width: 575px) {
	.payment-services-left, .payment-card-left{
		padding-top: 5px;
	}
	.payment-services-right{
		padding-left: 5px;
	}
}

/* payment-card */
.payment-card{
	position: relative;
	z-index: 0;
	margin-bottom: 17px;
	justify-content: space-between;
	&:after{
		content: '';
		position: absolute;
		top: -57px;
		left: 100%;
		transform: translateX(-240px);
		width: 595px;
		height: 595px;
		background: url(../images/circle.svg) center center no-repeat;
		background-size: contain;
		z-index: -1;
	}
	.card-image{
		img{
			margin-bottom: 32px;
			border-radius: 7px;
			box-shadow: 0px 39px 44px rgba(39, 69, 129, 0.31);
		}
	}
	.to-paid{
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-weight: bold;
		font-size: 14px;
		color: $color-1;
		border-bottom: 1px solid #EDEFF4;
		padding-bottom: 4px;
		margin-bottom: 19px;
		text-transform: uppercase;
		.summ{
			color: $color-2;
			font-size: 24px;
			span{
				font-size: 10px;
			}
		}
	}
}

.form-card{
	h4, .h4{
		span{
			font-size: 10px;
		}
	}
	.box-form{
		margin-bottom: 15px;
		padding-bottom: 25px;
	}
	.rules{
		text-align: center;
		margin-bottom: 15px;
	}
}

.payment-card-right{
	strong{
		color: $color-1;
		font-weight: 700;
	}
	li{
		img{
			margin-right: 13px;
		}
		.img-wrap{
			padding-top: 14px;
		}
	}
}
@media (max-width: 991px) {
	.payment-card .to-paid{
		padding-bottom: 23px;
		margin-bottom: 23px;
	}
	.payment-card{
		.card-image{
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			img{
				margin-bottom: 29px;
			}
		}
	}
}
@media (max-width: 767px) {
	.payment-card{
		&:after{
			display: none;
		}
	}
}
@media (max-width: 575px) {
	.form-card{
		h4, .h4{
			span{
				display: block;
			}
		}
	}
	.payment-card-right{
		font-size: 12px;
	}
}

/* faq */
.fade {
  transition: opacity 0.15s linear;
}

@media (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}

.fade:not(.show) {
  opacity: 0;
}

.collapse:not(.show) {
  display: none;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height 0.35s ease;
}

@media (prefers-reduced-motion: reduce) {
  .collapsing {
    transition: none;
  }
}

/* accordion */
.accordion{
	max-width: 796px;
	margin-bottom: 50px;
	> h4, > .h4{
		margin-bottom: 30px;
	}
	.accordion-card + {
		h4, .h4{
			margin-top: 37px;
		}
	}
}
.accordion-card{
	margin-bottom: 10px;
	background: #fff;
	box-shadow: 0px 4px 50px rgba(31, 78, 120, 0.08);
	border-radius: 8px 28px 28px 8px;
	.card-header{
		h3, .h3{
			margin-bottom: 0;
		}
		button{
			background: none;
			border: none;
			min-height: 54px;
			padding: 5px 60px 5px 18px;
			font-weight: 100;
			font-size: 14px;
			color: #2F2F2F;
			width: 100%;
			text-align: left;
			position: relative;
			font-weight: 700;
			.arrow{
				content: '';
				position: absolute;
				top: 50%;
				right: 9px;
				transform: translateY(-50%);
				border: 1px solid rgba(255, 255, 255, 0.89);
				box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54);
				width: 36px;
				height: 36px;
				border-radius: 50%;
				z-index: 1;
				background-color: #62C9AA;
				&:after{
					content: '';
					position: absolute;
					left: 50%;
					top: 50%;
					transform: translate(-50%,-50%) rotate(-90deg);
					width: 5px;
					height: 10px;
					background: url(../images/arrow.svg) center center no-repeat;
					background-size: contain;
				}
			}
			&.collapsed{
				font-weight: 100;
				.arrow:after{
					transform: translate(-50%,-50%) rotate(90deg);
				}
			}
		}
	}
	.card-body{
		padding: 15px 40px;
		position: relative;
		&:before{
			content: '';
			border-top: 1px solid #EDEFF4;
			position: absolute;
			left: 20px;
			right: 20px;
			top: 0;
		}
	}
}

@media (max-width: 575px) {
	.accordion{
		margin-bottom: 30px;
		> h4, > .h4{
			margin-bottom: 16px;
		}
		.accordion-card + {
			h4, .h4{
				margin-top: 24px;
			}
		}
	}
	.accordion-card{
		border-radius: 8px;
		.card-header{
			button{
				padding: 10px 60px 10px 10px;
				font-size: 12px;
				.arrow{
					width: 28px;
					height: 28px;
					&:after{
						height: 9px;
					}
				}
			}
		}
		.card-body{
			padding: 15px 10px;
			font-size: 12px;
			&:before{
				left: 0px;
				right: 0px;
			}
			h3{
				font-size: 10px;
				margin-bottom: 15px;
			}
		}
	}
}

/* successful-row */
.successful-page{
	h1{
		text-align: center;
	}
}
.successful-row{
	.image{
		flex: 0 0 43.6%;
		max-width: 43.6%;
	    text-align: center;
	    padding-top: 8px;
	    margin-bottom: 50px;
	}
	.text{
		flex: 0 0 56.4%;
		max-width: 56.4%;
		margin-bottom: 50px;
		strong{
			color: $color-1a;
			font-weight: 400;
		}
		p, ul{
			margin-bottom: 30px;
		}
		ul{
			margin-left: -28px;
		}
	}
	.successful-footer{
		display: flex;
		align-items: center;
		padding-top: 10px;
		span{
			padding: 0 10px;
		}
		.successful-btn{
			background: #FFFFFF;
			border: 1px solid #EDEFF4;
			border-radius: 7px;
			color: #2F2F2F;
			text-decoration: none;
			font-size: 14px;
			padding: 5px 16px;
			display: flex;
			justify-content: center;
			align-items: center;
			min-height: 67px;
			width: 278px;
			text-align: left;
			font-weight: 100;
			&:before{
				content: '';
				display: inline-block;
				min-width: 37px;
				height: 32px;
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
				margin-right: 14px;
			}
			&:hover{
				box-shadow: 0px 4px 63px rgba(31, 78, 120, 0.07);
			}
			&:active{
				background: #FBFBFB;
			}
			&.to-email{
				&:before{
					background-image: url(../images/icon-mail.svg);
				}
			}
			&.to-print{
				&:before{
					background-image: url(../images/icon-print.svg);
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.successful-row{
		.image{
		    margin-bottom: 65px;
		}
		.text{
			margin-bottom: 20px;
			ul{
				margin-left: 0;
			}
		}
		.successful-footer{
			flex-wrap: wrap;
			span{
				display: none;
			}
			.successful-btn{
				margin-bottom: 10px;
			}
		}
	}
}
@media (max-width: 767px) {
	.successful-row{
		.image{
		    flex: 0 0 100%;
			max-width: 100%;
			img{
				max-width: 278px;
			}
		}
		.text{
			flex: 0 0 100%;
			max-width: 100%;
		}
		.successful-footer{
			padding-top: 0;
			justify-content: center;
		}
	}
}

/* contacts */
