	h3, .h3{
		text-transform: uppercase;
		margin-bottom: 9px;
		font-size: 18px;
	}
	.desc{
		margin-bottom: 15px;
	}
}

@media (max-width: 1199px) {
	.contacts-form{
		padding: 40px 30px;
	}
}
@media (max-width: 575px) {
	.contacts-row{
		padding-top: 5px;
	}
	.contacts-box{
		padding-top: 24px;
		padding-bottom: 24px;
		p{
			margin-bottom: 1.5em;
		}
	}
	.contacts-form{
		text-align: center;
		padding: 24px 10px;
		h3, .h3{
			font-size: 14px;
			margin-bottom: 15px;
			text-align: left;
		}
		.desc{
			display: none;
		}
	}
	.map-box{
		padding: 10px;
	}
}

/* data */
.verification-box{
	position: relative;
	&:before{
		content: '';
		position: absolute;
		left: 100%;
		top: 0;
		width: 308px;
		height: 308px;
		background: url(../images/circle2.svg) center center no-repeat;
		background-size: contain;
		z-index: -1;
		transform: translate(-145px,-99px);
	}
}
.data-row{
	margin-bottom: 21px;
	line-height: 1.6;
	color: #84838A;
	&:last-child{
		margin-bottom: 0;
	}
	.data{
		display: flex;
	}
	.row-name{
		font-weight: 500;
		padding-right: 5px;
		white-space: nowrap;
	}
	.btn-edit{
		margin-left: 6px;
		width: 21px;
		height: 21px;
		display: flex;
		justify-content: center;
		align-items: center;
		background: none;
		border: none;
		svg{
			width: 16px;
		}
		path{
			fill: #C4C4C4;
			transition-duration: 0.2s;
		}
		&:hover{
			path{
				fill: $color-2;
			}
		}
	}

	.data-edit{
		display: none;
	}

	&.edited{
		.data{
			.val, .btn-edit{
				display: none;
			}
			.row-name{
				font-weight: 100;
				font-size: 10px;
				line-height: 20px;
				text-transform: uppercase;
				color: #2F2F2F;
				margin-bottom: 3px;
			}
		}
		.data-edit{
			display: block;
		}
	}
}
.box-payment-metods{
	box-shadow: 0px 20px 65px #B4C5EA;
	margin-bottom: 25px;
}
.payment-metods{
	display: flex;
	flex-wrap: wrap;
	margin-right: -5px;
	margin-left: -5px;
	padding-top: 6px;
	.metod{
		padding: 0 5px;
		display: flex;
		margin-bottom: 10px;
		position: relative;
		flex: 0 0 25%;
		max-width: 25%;
		&.full{
			flex: 0 0 100%;
			max-width: 100%;
			label img{
				border-radius: 5px;
				box-shadow: 0px 9px 15px rgba(39, 69, 129, 0.31);
			}
		}
	}
	input[type='radio'] {
		width: 17px;
		height: 17px;
	}
	input[type='radio'] + label {
		cursor: pointer;
		padding: 11px 7px;
		position: relative;
		background: #FFFFFF;
		border: 1px solid #fff;
		box-shadow: 0px 0px 8px rgba(31, 78, 120, 0.13);
		border-radius: 16px;
		margin: 0;
		display: block;
		font-size: 16px;
		text-transform: none;
		color: #2F2F2F;
		font-weight: 300;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		transition-duration: 0.2s;
		span{
			padding-left: 15px;
		}
		&:before{
			content: '';
			position: absolute;
			right: 3px;
			top: 3px;
			border-radius: 50%;
			transform: translate(50%,-50%);
			background-color: #62C9AA;
			box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54), 0 0 0 5px #fff;
			border: 1px solid rgba(255, 255, 255, 0.89);
			width: 21px;
			height: 21px;
			background-position: center;
			background-repeat: no-repeat;
			background-size: 10px;
			background-image: url(../images/check.svg);
			opacity: 0;
		}
		&:hover{
			box-shadow: 0px 0px 8px rgba(31, 78, 120, 0.38);
		}
	}
	input[type='radio']:not(checked) {
	  position: absolute;
	  opacity: 0;
	  left: 0;
	  top: 0;
	  z-index: -1;
	}
	input[type='radio']:checked + label {
		border-color: $color-2;
		&:before{
			opacity: 1;
		}
	}

}
@media (min-width: 1200px) {
	.verification-box{
		.row{
			margin-left: -42px;
			margin-right: -42px;
			.col-6{
				padding: 0 42px;
			}
		}
	}
}
@media (max-width: 1199px) {
	.data-row{
		.data{
			width: 100%;
			flex-wrap: wrap;
			padding-right: 25px;
			position: relative;
		}
		.row-name{
			padding-right: 5px;
			white-space: normal;
		}
		.btn-edit{
			position: absolute;
			top: 1px;
			right: 0;
		}
	}
}
@media (max-width: 767px) {
	.verification-box{
		&:before{
			display: none;
		}
		.col-6{
			margin-bottom: 24px;
			&:last-child{
				margin-bottom: 0;
			}
		}
	}
}
@media (max-width: 767px) {
	.data-row{
		margin-bottom: 17px;
	}
	.payment-metods{
		.metod{
			flex: 0 0 100%;
			max-width: 100%;
			label img{
				max-height: 27px;
			}
		}
		input[type='radio'] + label {
			font-size: 14px;
		}
	}
	.payment-metods input[type='radio'] + label:before{
		box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54);
	}
}

@import "footer";
@import "modal";