@import "variables";

/* footer */
.main-footer{
	margin-top: auto;
	padding: 8px 0px;
	background: $color-1;
	@include clearfix;
	a{
		color: #fff;
		&:hover{
			color: #fff;
		}
	}
	p{
		margin-bottom: 7px;
		&:last-child{
			margin: 0;
		}
	}
	.footer-container{
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.image-row{
		display: flex;
		.img{
			margin-right: 10px;
			display: flex;
			align-items: center;
			justify-content: center;
			span, a{
				background: #fff;
				padding: 2px;
				border-radius: 2px;
				width: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 24px;
			}
		}
		img{
			max-height: 100%;
		}
	}
	.copyright{
		text-align: right;
		font-weight: 300;
		font-size: 10px;
		color: #fff;
	}
}

@media (max-width: 991px){
	.main-footer{
		padding: 20px 0 13px;
		.footer-container{
			flex-wrap: wrap;
		}
		.image-row{
			justify-content: center;
			margin-bottom: 14px;
			width: 100%;
		}
		.copyright{
			text-align: center;
			width: 100%;
		}
	}
}
@media (max-width: 575px){
	.main-footer{
		.image-row{
			margin-bottom: 10px;
			flex-wrap: wrap;
			.img{
				width: 20%;
				margin: 0 0 4px;
				padding: 0 2px;
				span, a{
					height: 32px;
					padding: 5px 4px;
				}
			}
		}
	}
}