@import "variables";

/* HEADER */
.main-header{
  background: $color-1;
  position: relative;
  .header-container{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header-right{
    display: flex;
    align-items: center;
  }
  .logo{
    display: block;
    max-width: 106px;
    margin-right: 38px;
    img{
      width: 100%;
    }
  }
  .btn-callback{
    color: #fff;
    font-size: 12px;
    font-weight: 300;
    display: block;
    line-height: 50px;
    padding: 0 17px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    margin-left: 23px;
    &:before{
      content: '';
      display: block;
      width: 11px;
      height: 22px;
      background: url(../images/callback.svg) center no-repeat;
      background-size: contain;
      margin-right: 8px;
    }
    &:hover{
      background: $color-1a;
    }
  }
}

.main-nav{
  ul{
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    li{
      position: relative;
      a{
        font-weight: 300;
        font-size: 12px;
        color: #fff;
        text-decoration: none;
        padding: 5px 17px;
        display: block;
      }
      &.current-menu-item{
        > a{
          
        }
      }
      &:hover{
        > a{
          text-decoration: underline;
        }
      }
    }
    ul{
      display: none;
    }
  }
}

.main-header{
  .search-wrap{
    margin-right: auto;
    ::-webkit-input-placeholder {color:rgba(255, 255, 255, 0.4);}
    ::-moz-placeholder          {color:rgba(255, 255, 255, 0.4);}
    :-moz-placeholder           {color:rgba(255, 255, 255, 0.4);}
    :-ms-input-placeholder      {color:rgba(255, 255, 255, 0.4);}
  }
  .search{
    .form{
      display: flex;
      background: #173F62;
      border: 1px solid rgba(255, 255, 255, 0.25);
      box-sizing: border-box;
      border-radius: 23px;
      padding-right: 10px;
      width: 282px;
    }
    input[type='search'], input[type='text']{
      background: none;
      border: none;
      color: #fff;
      font-weight: 300;
      font-size: 12px;
      padding: 0 0 0 23px;
      height: 30px;
      width: 100%;
    }
    .search-submit{
      border: none;
      height: 30px;
      width: 30px;
      cursor: pointer;
      background: url(../images/search.svg) center no-repeat;
      background-size: 17px;
    }
  }
  .search-trigger{
    display: none;
    height: 42px;
    width: 42px;
    cursor: pointer;
    border: none;
    background: url(../images/search.svg) center 11px no-repeat;
    background-size: 20px;
    position: relative;
    transition-duration: 0s;
    &.search-active{
      background: none;
      &:after, &:before{
        content: '';
        width: 20px;
        height: 1px;
        background-color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transition-duration: 0.2s;
        transform-origin: center center;
      }
      &:before{
        transform: translate(-50%,-50%) rotate(45deg);
      }
      &:after{
        transform: translate(-50%,-50%) rotate(-45deg);
      }
    }
  }
}


/* NAV */
.nav-trigger{
  border: none;
  background: none;
  cursor: pointer;
  margin: 0;
  display: none;
  background: $color-1a;
  width: 42px;
  height: 42px;
  position: relative;
  span{
    width: 20px;
    height: 1px;
    background: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -10px;
    transition-duration: 0.2s;
    transform-origin: center center;
    &:nth-child(1){
      margin-top: -6px;
    }
    &:nth-child(3){
      margin-top: 6px;
    }
  }
  &.active{
    span{
      &:nth-child(1){
        margin-top: 0;
        transform: rotate(45deg);
      }
      &:nth-child(2){
        opacity: 0;
      }
      &:nth-child(3){
        margin-top: 0;
        transform: rotate(-45deg);
      }
    }
  }
}
@media (max-width: 1199px) {
  .main-header .logo{
    margin-right: 28px;
  }
  .main-header .search .form{
    width: 200px;
  }
  .main-nav ul li a {
      padding: 5px 10px;
  }
  .main-header .btn-callback{
    margin-left: 15px;
  }
}
@media (max-width: 991px) {
  .nav-trigger{
    display: block;
  }

  .main-header{
    .logo{
      max-width: 82px;
      margin-right: 15px;
    }
    .search-wrap{
      margin-left: auto;
      margin-right: 0;
      &.active{
        display: block;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;
        width: 100%;
        max-width: 696px;
        display: flex;
        height: 42px;
        align-items: center;
        background: $color-1;
        padding: 0 10px 0 0;
        .search{
          display: block;
          width: 100%;
        }
      }
    }
    .search{
      display: none;
      .form{
        width: 100%;
      }
    }
    .search-trigger{
      display: block;
    }
  }

  .main-header{
    .header-right{
      display: block;
      position: fixed;
      top: 42px;
      left: 0;
      right: 0;
      z-index: 100;
      background: #24435F;
      overflow-y: auto;
      height: calc(100% - 42px);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      visibility: hidden;
      opacity: 0;
      transition: opacity 0.1s ease-in, visibility 0s ease-in 0.1s;
      &:before{
        content: '';
        position: absolute;
        left: 0;
        top: -5px;
        right: 0;
        box-shadow: 0px -1px 11px rgba(0, 0, 0, 0.25);
        z-index: 1;
        height: 5px;
      }
    }

    .btn-callback{
      width: 100%;
      margin: 0;
      background-color: $color-1a;
    }
  }

  #main-header.active{
    .header-right{
      visibility: visible;
      opacity: 1;
      transition-delay: 0s;
    }
  }

  .main-nav{
    width: 100%;
    margin-bottom: 15px;
    ul{
      flex-wrap: wrap;
      li{
        width: 100%;
        margin-bottom: 15px;
        &.menu-item-has-children{
          background: $color-1;
          border-bottom: 1px solid $color-1a;
          padding: 29px 0 64px;
          margin-bottom: 27px;
          >a{
            font-size: 12px;
            text-transform: uppercase;
            font-weight: 700;
            margin-bottom: 10px;
          }
        }
        a{
          padding: 7px 20px;
          text-align: center;
          font-size: 14px;
          text-decoration: none !important;
        }
        ul{
          display: block;
          li{
            padding: 0 20px;
            margin-bottom: 10px;
            a{
              border: 1px solid #3B7BB5;
              border-radius: 8px;
              font-weight: 100;
              padding: 12px 10px;
              box-shadow: 0px 0px 8px rgba(31, 78, 120, 0.13);
            }
            &.current-menu-item{
              > a{
                background: rgba(59, 123, 181, 0.56);
              }
            }
          }
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .main-header{
    .search-wrap{
      &.active{
        max-width: 516px;
      }
    }
  }
}
@media (max-width: 575px) {
   .main-header{
    .search-wrap{
      &.active{
        max-width: none;
      }
    }
  }
  .nav-trigger{
    margin-right: -10px;
  }
}
