@import "variables";

/* modal */
.modal-open {
  overflow: hidden;
}

.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0;
}

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem auto;
  pointer-events: none;
}

.modal.fade .modal-dialog {
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  -webkit-transform: translate(0, -50px);
  transform: translate(0, -50px);
}

@media (prefers-reduced-motion: reduce) {
  .modal.fade .modal-dialog {
    transition: none;
  }
}

.modal.show .modal-dialog {
  -webkit-transform: none;
  transform: none;
}

.modal.modal-static .modal-dialog {
  -webkit-transform: scale(1.02);
  transform: scale(1.02);
}

.modal-dialog-scrollable {
  display: -ms-flexbox;
  display: flex;
  max-height: calc(100% - 1rem);
}

.modal-dialog-scrollable .modal-content {
  max-height: calc(100vh - 1rem);
  overflow: hidden;
}

.modal-dialog-scrollable .modal-header,
.modal-dialog-scrollable .modal-footer {
  -ms-flex-negative: 0;
  flex-shrink: 0;
}

.modal-dialog-scrollable .modal-body {
  overflow-y: auto;
}

.modal-dialog-centered {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  min-height: calc(100% - 1rem);
}

.modal-dialog-centered::before {
  display: block;
  height: calc(100vh - 1rem);
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  content: "";
}

.modal-dialog-centered.modal-dialog-scrollable {
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  height: 100%;
}

.modal-dialog-centered.modal-dialog-scrollable .modal-content {
  max-height: none;
}

.modal-dialog-centered.modal-dialog-scrollable::before {
  content: none;
}

.modal-content {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-clip: padding-box;
  outline: 0;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
}

.modal-backdrop.fade {
  opacity: 0;
}

.modal-backdrop.show {
  opacity: 0;
}

.fade {
  transition: opacity 0.15s linear;
}

@media (prefers-reduced-motion: reduce) {
  .fade {
    transition: none;
  }
}

.fade:not(.show) {
  opacity: 0;
}

@media (min-width: 576px) {
  .modal-dialog {
    margin: 1.75rem auto;
  }
  .modal-dialog-scrollable {
    max-height: calc(100% - 3.5rem);
  }
  .modal-dialog-scrollable .modal-content {
    max-height: calc(100vh - 3.5rem);
  }
  .modal-dialog-centered {
    min-height: calc(100% - 3.5rem);
  }
  .modal-dialog-centered::before {
    height: calc(100vh - 3.5rem);
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
  }
}

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

/**/
.modal-open{
  .main{
    filter: blur(10px);
  }
}
.modal-content .close{
  position: absolute;
  right: 13px;
  top: 13px;
  width: 30px;
  height: 30px;
  border: none;
  background: none;
  cursor: pointer;
  &:after, &:before{
    content: '';
    width: 22px;
    height: 1px;
    background-color: #C4C4C4;
    position: absolute;
    top: 50%;
    left: 50%;
    transition-duration: 0.2s;
    transform-origin: center center;
  }
  &:before{
    transform: translate(-50%,-50%) rotate(45deg);
  }
  &:after{
    transform: translate(-50%,-50%) rotate(-45deg);
  }
  &:hover{
    &:after, &:before{
      background-color: $color-2;
    }
  }
}

.modal-dialog {
    max-width: 552px;
} 
.modal-content{
  background: #FFFFFF;
  box-shadow: 0px 4px 65px rgba(31, 78, 120, 0.4);
  border-radius: 8px;
  position: relative;
  padding: 31px;
  &:before{
    content: '';
    position: absolute;
    left: 100%;
    top: 0;
    width: 308px;
    height: 308px;
    background: url(../images/circle2.svg) center center no-repeat;
    background-size: contain;
    z-index: -1;
    transform: translate(-173px,-117px);
  }

  .modal-header{
    margin-bottom: 18px;
    text-align: center;
    .modal-title{
      font-weight: bold;
      font-size: 18px;
      color: $color-1;
      margin: 0;
      text-transform: uppercase;
      line-height: 1.7em;
    }
    .desc{
      font-size: 12px;
      line-height: 22px;
      padding-top: 5px;
    }
  }
}
.modal-sm{
  max-width: 408px;
  .modal-content{
    padding: 36px 48px 45px;
    .modal-header{
      margin-bottom: 10px;
    }
  }
}
.modal-lg{
  max-width: 1080px;
  .modal-content{
    padding: 36px 48px 40px;
    .modal-header{
      margin-bottom: 6px;
    }
  }
}
.send-to-email{
  .modal-content{
    &:before{
      display: none;
    }
  }
}

@media (max-width: 1199px) {
  .modal-lg{
    max-width: 900px;
    .modal-content{
      padding: 36px 40px 40px;
    }
  }
}
@media (max-width: 991px) {
  .modal-lg{
    max-width: 650px;
    .modal-content{
      padding: 30px;
    }
  }
  .modal-content{
    &:before{
      display: none;
    }
  }
}
@media (max-width: 430px) {
  .modal-content .close{
    right: 3px;
    top: 3px;
  }
/*.modal-dialog-centered{
    align-items: flex-start;
    min-height: calc(100% - 0.5rem - 55px);
  }*/
  .modal-dialog {
    max-width: 100%;
    margin-right: 10px;
    /*margin-top: 55px;*/
  }
  .modal-content{
    min-height: 536px;
    border-radius: 0 8px 8px 0;
    padding: 30px 10px 30px 20px;
    .modal-header{
      margin-bottom: 24px;
      text-align: left;
      .modal-title{
        font-size: 14px;
      }
      .desc{
        display: none;
      }
    }
  }
  .modal-sm, .modal-lg{
    .modal-content{
      padding: 30px 10px 30px 20px;
    }
  }
  .send-to-email{
    .modal-content{
      overflow: hidden;
      z-index: 0;
      justify-content: center;
      &:before{
        content: '';
        position: absolute;
        display: block;
        left: 0%;
        top: 0%;
        width: 272px;
        height: 272px;
        background: url(../images/circle3.svg) center center no-repeat;
        background-size: contain;
        z-index: -1;
        transform: translate(-63%,-50%);
      }
      &:after{
        content: '';
        position: absolute;
        left: 100%;
        top: 100%;
        width: 272px;
        height: 272px;
        background: url(../images/circle3.svg) center center no-repeat;
        background-size: contain;
        z-index: -1;
        transform: translate(-50%,-50%);
      }
      .modal-header, .modal-body{
        position: relative;
        z-index: 1;
      }
    }
  }

}