@import "variables";

/* forms */
input[type='text'], input[type='tel'], input[type='search'], input[type='email'], input[type='password'], input[type='number'], input[type='date'], textarea, select{
	background: #F3F6FF;
	border: 1px solid #C4C4C4;
	border-radius: 5px;
	margin: 0;
	width: 100%;
	height: 40px;
	padding: 0 14px;
	font-family: $font-1;
	font-size: 12px;
	font-weight: 100;
	display: block;
	color: #2F2F2F;
	&:focus{
		border-color: $color-1;
	}
	&.error{
		border-color: #FF8B8B;
	}
}
textarea{
	padding: 9px 14px;
	height: 70px;
	resize: none;
}

::-webkit-input-placeholder {color:#84838A;transition-duration:0.2s;}
::-moz-placeholder          {color:#84838A;transition-duration:0.2s;}
:-moz-placeholder           {color:#84838A;transition-duration:0.2s;}
:-ms-input-placeholder      {color:#84838A;transition-duration:0.2s;}

:focus::-webkit-input-placeholder {opacity: 0.8;}
:focus::-moz-placeholder          {opacity: 0.8;}
:focus:-moz-placeholder           {opacity: 0.8;}
:focus:-ms-input-placeholder      {opacity: 0.8;}

.form-group{
  margin-bottom: 22px;
  span.error, label.error{
  	font-size: 12px;
  	text-align: right;
  	display: block;
  	color: #FF8B8B;
  	font-weight: 300;
  }
}
label, .label{
	font-weight: 100;
	font-size: 10px;
	line-height: 20px;
	text-transform: uppercase;
	color: #2F2F2F;
	margin-bottom: 3px;
	display: block;
	text-align: left;
	&.error{
		font-weight: 300;
		color: #FF8B8B;
	}
}

.input-group {
    position: relative;
    display: flex;
    align-items: stretch;
    width: 100%;
    .input-group-text {
    	min-width: 49px;
	    margin-left: -1px;
	    display: flex;
	    align-items: center;
	    justify-content: center;
	    padding: 5px;
	    margin-bottom: 0;
	    font-weight: 100;
		font-size: 10px;
		text-transform: uppercase;
		color: #2F2F2F;
	    text-align: center;
	    white-space: nowrap;
	    background: #F3F6FF;
		border: 1px solid #C4C4C4;
	    border-radius: 0 5px 5px 0; 
	}
	input{
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
	}
}

/* button */
.btn{
	display: inline-block;
	padding: 12px 15px;
	min-width: 192px;
	margin: 0px;
	color: #fff;
	background: #62C9AA;
	border: 1px solid #FFFFFF;
	border-radius: 29px;
	font-weight: 300;
	font-size: 12px;
	line-height: 1.35em;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	transition-duration: 0.2s;
	vertical-align: top;
	box-shadow: 0px 4px 12px rgba(98, 201, 170, 0.54);
	&:hover{
		color: #fff;
	}
	&:focus{
	}
	&:active{
		background-color: #3FB994;
	}
}

.form-footer{
	display: flex;
	align-items: center;
	justify-content: space-between;
	&:after{
		content: '';
		flex: 1;
	}
	.back-wrap{
		flex: 1;
	}
}

.back{
	color: #2F2F2F;
	font-size: 14px;
	font-weight: 100;
	position: relative;
	display: inline-block;
	padding-left: 10px;
	text-decoration: none;
	margin-right: 14px;
	&:before{
		content: '';
		position: absolute;
		left: -4px;
		top: 50%;
		transform: translateY(-50%);
		border: 5px solid transparent;
		border-left-width: 4px;
		border-right-width: 4px;
		border-right-color: #2F2F2F;
		transition-duration: 0.2s;
	}
	&:after{
		content: '';
		position: absolute;
		left: 10px;
		right: 0;
		bottom: 0;
		border-bottom: 1px solid #2F2F2F;
		transition-duration: 0.2s;
	}
	&:hover{
		&:before{
			left: -8px;
		}
	}
}

.box-form{
	padding: 39px 47px 32px;
	margin-bottom: 20px;
	.title, h3, .h3{
		font-size: 18px;
		text-transform: uppercase;
		margin-bottom: 30px;
		font-weight: 700;
		color: $color-1;
	}
}
.form-row{
	margin-right: -8px;
	margin-left: -8px;
	.col-6, .col-4{
		padding: 0 8px;
	}
}

@media (max-width: 1199px) {
	.box-form{
		padding: 30px 30px;
		.title, h3, .h3{
			margin-bottom: 16px;
		}
	}
}
@media (max-width: 991px) {
	
}
@media (max-width: 767px) {
	
}
@media (max-width: 575px) {
	textarea{
		height: 114px;
	}
	.form-group{
	  margin-bottom: 18px;
	}
	.form-row{
		margin-right: -5px;
		margin-left: -5px;
		.col-6, .col-4{
			padding: 0 5px;
		}
	}
	.box-form{
		padding: 25px 10px 30px;
		margin-bottom: 10px;
		h2, .h2{
			margin-bottom: 15px;
		}
	}
}
@media (max-width: 450px) {
	.form-footer{
		justify-content: center;
		flex-direction: column;
		&:after{
			display: none;
		}
		.back-wrap{
			flex: 0;
			order: 4;
			padding-top: 20px;
		}
	}
}