@import "variables";




@font-face {
    font-family: 'e-Ukraine';
    src: url('../fonts/e-Ukraine-Light.eot');
    src: local('e-Ukraine Light'), local('e-Ukraine-Light'),
        url('../fonts/e-Ukraine-Light.eot?#iefix') format('embedded-opentype'),
        url('../fonts/e-Ukraine-Light.woff2') format('woff2'),
        url('../fonts/e-Ukraine-Light.woff') format('woff'),
        url('../fonts/e-Ukraine-Light.ttf') format('truetype'),
        url('../fonts/e-Ukraine-Light.svg#e-Ukraine-Light') format('svg');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'e-Ukraine';
    src: url('../fonts/e-Ukraine-Regular.eot');
    src: local('e-Ukraine Regular'), local('e-Ukraine-Regular'),
        url('../fonts/e-Ukraine-Regular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/e-Ukraine-Regular.woff2') format('woff2'),
        url('../fonts/e-Ukraine-Regular.woff') format('woff'),
        url('../fonts/e-Ukraine-Regular.ttf') format('truetype'),
        url('../fonts/e-Ukraine-Regular.svg#e-Ukraine-Regular') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'e-Ukraine';
    src: url('../fonts/e-Ukraine-Bold.eot');
    src: local('e-Ukraine Bold'), local('e-Ukraine-Bold'),
        url('../fonts/e-Ukraine-Bold.eot?#iefix') format('embedded-opentype'),
        url('../fonts/e-Ukraine-Bold.woff2') format('woff2'),
        url('../fonts/e-Ukraine-Bold.woff') format('woff'),
        url('../fonts/e-Ukraine-Bold.ttf') format('truetype'),
        url('../fonts/e-Ukraine-Bold.svg#e-Ukraine-Bold') format('svg');
    font-weight: bold;
    font-style: normal;
    font-display: swap;
}

@font-face {
    font-family: 'e-Ukraine';
    src: url('../fonts/e-Ukraine-Thin.eot');
    src: local('e-Ukraine Thin'), local('e-Ukraine-Thin'),
        url('../fonts/e-Ukraine-Thin.eot?#iefix') format('embedded-opentype'),
        url('../fonts/e-Ukraine-Thin.woff2') format('woff2'),
        url('../fonts/e-Ukraine-Thin.woff') format('woff'),
        url('../fonts/e-Ukraine-Thin.ttf') format('truetype'),
        url('../fonts/e-Ukraine-Thin.svg#e-Ukraine-Thin') format('svg');
    font-weight: 100;
    font-style: normal;
    font-display: swap;
}

*{
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}
img{
	border:0;
	max-width:100%;
	height: auto;
}
a{
	color: $color-1a;
	text-decoration: underline;
	transition: color 0.2s, background 0.2s, border 0.2s;
	&:hover{
		color: $color-1;
		text-decoration: none;
	}
	&:focus{
	 	text-decoration: none;
	}
}
button{
	transition: color 0.2s, background 0.2s, border 0.2s;
}
*:focus,*:active, * {
	outline: none !important;
}
*::-moz-selection {
  background: rgba(30, 165, 230, 0.25); }
*::selection {
  background: rgba(30, 165, 230, 0.25); }

body{
	font-size: 14px;
	font-family: $font-1;
	font-weight: 100;
	line-height: 1.5;
	color: #2F2F2F;
	background: #f5f6fa url(../images/bg.jpg) center top no-repeat;
	padding: 0;
	&.menu-active{
		overflow: hidden;
	}
}

body, html{
	height: 100%;
}

#wrapper{
	overflow: hidden;
	position: relative;
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

.container{
	margin: 0 auto;
	padding: 0 12px;
}

@media (min-width:576px){
	.container{max-width: 540px;}
}
@media (min-width:768px){
	.container{max-width:720px}
}
@media (min-width:992px){
	.container{max-width:960px}
}
@media (min-width: 1200px){
	.container{max-width: 1152px}
}

.row{
	display: flex;
	flex-wrap: wrap;
	margin: 0 -12px;
}
.col, .col-3, .col-4, .col-6, .col-12{
	padding: 0 12px;
}
.col-3{
	flex: 0 0 25%;
	max-width: 25%;
}
.col-4{
	flex: 0 0 33.33%;
	max-width: 33.33%;
}
.col-6{
	flex: 0 0 50%;
	max-width: 50%;
}
.col-12{
	flex: 0 0 100%;
	max-width: 100%;
}

.main{
  padding: 53px 0 34px;
}

.sidebar{
	padding: 0 12px;
	flex: 0 0 25%;
	max-width: 25%;
}
.main-content{
	padding: 0 12px;
	flex: 0 0 75%;
	max-width: 75%;
}

@media (max-width: 1199px) {

}
@media (max-width: 991px) {
	.main{
	  padding-top: 45px;
	}
	.sidebar{
		flex: 0 0 33.33%;
		max-width: 33.33%;
	}
	.main-content{
		flex: 0 0 66.66%;
		max-width: 66.66%;
	}
}
@media (max-width: 767px) {
	body{
		background-image: url(../images/bg-mob.jpg);
	}
	.sidebar{
		flex: 0 0 100%;
		max-width: 100%;
		display: none;
	}
	.main-content{
		flex: 0 0 100%;
		max-width: 100%;
	}
	.col-6, .col-4{
		flex: 0 0 100%;
		max-width: 100%;
	}
}
@media (max-width: 575px) {
	.main{
	  padding-top: 15px;
	}
	.container{
		margin: 0 auto;
		padding: 0 10px;
	}
	.row{
		display: flex;
		flex-wrap: wrap;
		margin: 0 -5px;
	}
	.col, .col-3, .col-4, .col-6, .col-12{
		padding: 0 5px;
	}
	.sidebar{
		padding: 0 5px;
	}
	.main-content{
		padding: 0 5px;
	}
}

iframe{
	display: block;
	max-width: 100%;
	width: 100%;
	margin-bottom: 10px;
	border-radius: 4px;
	height: 345px;
}

@media (max-width: 991px){
	iframe{
		height: 250px;
	}
}
@media (max-width: 420px){
	iframe{
		height: 175px;
	}
}

/**/
h1, h2, h3, h4, h5, .h1, .h2, .h3, .h4, .h5{
	margin-top: 0px;
    margin-bottom: 1.5em;
}
h1, .h1{
	font-weight: bold;
	font-size: 32px;
	line-height: 1.3em;
	color: $color-1;
	margin-bottom: 1.5em;
	text-transform: uppercase;
}
h2, .h2{
	font-weight: bold;
	font-size: 28px;
	text-transform: uppercase;
	line-height: 1.4em;
	color: $color-1;
	margin-bottom: 1.2em;
}
h3, .h3{
	font-weight: bold;
	font-size: 24px;
	line-height: 1.4em;
	color: $color-1;
	margin-bottom: 1.2em;
	text-transform: uppercase;
}
h4, .h4{
	font-weight: 700;
	font-size: 14px;
	color: $color-1;
	margin-bottom: 14px;
	text-transform: uppercase;
}
h5, .h5{
	font-weight: 700;
	font-size: 12px;
	color: $color-1;
	text-transform: uppercase;
	margin-bottom: 17px;
}
h6, .h6{
	font-weight: 400;
	font-size: 14px;
	color: $color-1;
	margin-bottom: 8px;
}
p{
	margin: 0 0 8px;
}

/* table */
table{
	border-collapse: collapse;
	width: 100%;
	margin: 0px 0 15px;
}
table th{
	padding: 5px 5px;
	text-align: center;
	font-size: 14px;
	line-height: 1.3em;
}
table td{
	padding: 0px 5px;
	font-size: 12px;
}

.text-wrap{
	line-height: 1.6em;
	@include clearfix;
	ol{
		margin-bottom: 8px;
		list-style-position: inside;
	}
	ul{
		margin-bottom: 8px;
		list-style: none;
		li{
			position: relative;
			padding-left: 28px;
			&:before{
				content: '';
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background: $color-1a;
				position: absolute;
				left: 0;
				top: 8px;
			}
		}
	}
	ol{
		counter-reset: myCounter;
		> li{
			list-style: none;
			padding-left: 31px;
			position: relative;
			&:before{
				counter-increment: myCounter;
		        content: counter(myCounter) ".";
		        color: $color-1;
		        font-weight: 700;
		        position: absolute;
		        left: 0;
		        top: 0;
			}
			ol{
				counter-reset: myCounter2;
				> li{
					padding-left: 36px;
				}
				> li:before {
					counter-increment: myCounter2;
					content: counter(myCounter) "." counter(myCounter2);
				}
			}
		}
	}
	ul, ol{
		ul, ol{
			padding-top: 24px;
		}
		strong{
			color: $color-1;
		}
	}
	li, p{
		margin-bottom: 24px;
	}
	h2, .h2{
		margin-bottom: 25px;
	}
	h3, .h3{
		margin-bottom: 20px;
	}
	p + h3, p + .h3, ul + h3, ul + .h3, ol + h3, ol + .h3{
		margin-top: 29px;
	}
	strong{
		font-weight: 700;
	}
	hr{
	    border: none;
	    border-bottom: 1px solid #E5E5E5;
	    margin: 15px 0;
	}
	blockquote{
		padding-left: 27px;
		margin-bottom: 24px;
		color: $color-1a;
		font-weight: 400;
		position: relative;
		&:before{
			content: '';
			position: absolute;
			left: 0;
			top: 5px;
			bottom: 5px;
			border-left: 2px solid $color-1a;
		}
	}
	.red{
		color: #D30000;
		font-weight: 400;
	}
}

.text-center{
	text-align: center;
}

@media (max-width: 991px){
	h1, .h1{
		font-size: 26px;
		margin-bottom: 1.2em;
	}
}
@media (max-width: 575px){
	h1, .h1{
		font-size: 16px;
		line-height: 28px;
		padding-bottom: 14px;
		position: relative;
		margin-bottom: 15px;
		&:after{
			content: "";
			position: absolute;
			left: -10px;
			right: -10px;
			bottom: 0;
			border-bottom: 1px solid #EDEFF4;
		}
	}
	h2, .h2{
		font-size: 18px;
		margin-bottom: 12px;
	}
	h3, .h3{
		font-size: 14px;
	}
	.text-wrap{
		blockquote{
			padding-left: 17px;
			&:before{
				left: 2px;
			}
		}
		ul{
			li{
				padding-left: 17px;
			}
		}
		ol{
			> li{
				padding-left: 19px;
				&:before{
					font-size: 10px;
					top: 2px;
				}
				ol{
					> li{
						padding-left: 24px;
					}
				}
			}
		}
	}
}

/* box */
.box-wrap{
	display: flex;
}
.box{
	background: #FFFFFF;
	box-shadow: 0px 4px 50px rgba(31, 78, 120, 0.08);
	border-radius: 8px;
	margin-bottom: 24px;
	padding: 35px 30px;
	width: 100%;
}
.intro-box{
	border-left: 2px solid $color-1;
	margin-bottom: 37px;
	line-height: 1.8em;
	padding: 2px 0 2px 16px;
	p:last-child{
		margin-bottom: 0;
	}
}
.contract-box{
	padding: 36px 40px 10px;
	line-height: 1.75;
	h2, .h2{
		font-size: 16px;
		padding-bottom: 10px;
   		margin-bottom: 26px;
   		position: relative;
   		&:after{
   			content: '';
   			position: absolute;
			left: 0px;
			right: 0px;
			bottom: 0;
			border-bottom: 1px solid #EDEFF4;
		}
	}
}
.details-box{
	color: #84838A;
	padding: 27px 0;
	margin-bottom: 10px;
	strong{
		font-weight: 500;
	}
	p{
		margin-bottom: 19px;
	}
}
.box-title{
	padding-bottom: 7px;
    margin-bottom: 22px;
	border-bottom: 1px solid #EDEFF4;
}
@media (max-width: 767px){
	.contract-box{
		padding: 30px 30px 10px;
	}
}
@media (max-width: 575px){
	.box{
		padding: 24px 10px;
		margin-bottom: 10px;
	}
	.intro-box{
		margin-bottom: 30px;
		line-height: 1.8em;
		padding: 0px 0 0px 16px;
		font-size: 12px;
	}
	.contract-box{
		font-size: 12px;
		h2{
			&:after{
				left: -10px;
				right: -10px;
			}
		}
	}
	.details-box{
		padding-bottom: 0;
	}
	.box-title{
		padding-bottom: 10px;
	    margin-bottom: 15px;
	}
}